.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: red; /* #222     */
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}


body {
  font-family: 'Open Sans', sans-serif;
  background-color: #f1f2f3;
}

header {
  color: #fff;
  background-color: #00457c;  /* #82169c  */
  padding: 13px 0 66px 0px;
  display: block;
  position: -webkit-sticky;
  position: sticky;
  top: .1px;
}

header p {
  width: 400px;
  margin: 0 auto;
  float: left;
}
header .wrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

header h1 {
  font-size: 25px;
  font-weight: 600;
  letter-spacing: 0.3px;
  display: inline-block;
  margin: 0;
  vertical-align: middle;
  
}

.fa {
  font-size: 22px;
  -webkit-transform: translateY(50deg);
      -ms-transform: translateY(50deg);
          transform: translateY(50deg);  
}

header i {
  vertical-align: middle;
  margin-left: 12px;
}

.container {
  display: -ms-flexbox;
  display: flex;
  max-width: 1100px;
  margin: 0 auto;
  padding-top: 30px;
}

.wrapper {
  display: inline;
  width: 550px;
  margin: 0 auto;
}

.add-item {
  border-radius: 2px;
  margin-right: 50px;
  background-color: #fff;  /* #f9edfc  rgb(237, 237, 237) */
  padding: 20px 10px 10px 10px;
  -webkit-box-shadow: 0 3px 4px 0 rgba(0,0,0,.14), 0 3px 3px -2px rgba(0,0,0,.12), 0 1px 8px 0 rgba(0,0,0,.2);
          box-shadow: 0 3px 4px 0 rgba(0,0,0,.14), 0 3px 3px -2px rgba(0,0,0,.12), 0 1px 8px 0 rgba(0,0,0,.2);
  margin-bottom: 35px; 
  margin-top: -10px;
  width: 35%;
  margin-right: auto;
  height: 320px;
  position: -webkit-sticky;
  position: sticky;
  top: 100px;
}

.display-totals {
  border-radius: 2px;
  margin-right: 25px;
  background-color: #fff;  /*  #f9edfc rgb(237, 237, 237) */
  padding: -10px 10px 10px 10px;
  -webkit-box-shadow: 0 3px 4px 0 rgba(0,0,0,.14), 0 3px 3px -2px rgba(0,0,0,.12), 0 1px 8px 0 rgba(0,0,0,.2);
          box-shadow: 0 3px 4px 0 rgba(0,0,0,.14), 0 3px 3px -2px rgba(0,0,0,.12), 0 1px 8px 0 rgba(0,0,0,.2);
  width: 355px;
  height: 150px;
}

.display-item {
  width: 65%;
  margin-left: 50px;
  display: block;
}

.display-item button {
  margin-top: 10px;
  margin-left: auto;
  float: right;
}

.add-item form {
  width: 100%;
  overflow: hidden;
}

input {
  font-size: 22px;
  color: #000;
  padding: 18px 22px;
  font-size: 16px;
  margin-bottom: 17px;
  border: 1;
  display: block;
  width: 100%;  
}

ul {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  margin: 0;
  padding: 0;
}

li {
  width: calc(50% - 180px);
  list-style-type: none;
  margin-right: 50px;
  background-color: #f9edfc;  /*  rgb(237, 237, 237)    */
  color: #000;
  margin-bottom: 30px;
  -webkit-box-shadow: 0 3px 4px 0 rgba(0,0,0,.14), 0 3px 3px -2px rgba(0,0,0,.12), 0 1px 8px 0 rgba(0,0,0,.2);
          box-shadow: 0 3px 4px 0 rgba(0,0,0,.14), 0 3px 3px -2px rgba(0,0,0,.12), 0 1px 8px 0 rgba(0,0,0,.2);
}

h2 {
  padding: 0;
  margin: 0;
  font-weight: 400;
}

li h3 {
  background-color: #82169c;  /* #103D5D  */
  margin: 0;
  color: #fff;
  font-weight: 300;
  padding: 15px;
}

li p {
  padding: 15px;
}

.li-p-button {
  -ms-flex-item-align:end;
      align-self:flex-end;
}

form button {
  width: 100%;
  margin-top: 10px;
}

button {
  border-radius: 2px;
  min-width: 88px;
  background-color: #00457c;  /*  #FA6900    */
  cursor: pointer;
  border: 0;
  min-width: 120px;
  color: #fff;
  font-size: 16px;
  -webkit-box-shadow: 0 2px 5px 0 rgba(0,0,0,.26);
          box-shadow: 0 2px 5px 0 rgba(0,0,0,.26);
  padding: 10px 6px;
}

.user-profile {
  position: fixed;
  bottom: 20px;
  right: 40px;
  width: 100px;
  height: 100px;
}

.user-profile img {
  border-radius: 50%;
  max-width: 100%;
}


